import React, { useState, useEffect } from 'react';
import {
    Box,
    List,
    ListItem,
    ListItemText,
    Typography,
    CircularProgress,
    Alert,
    Chip,
    Divider,
    Pagination,
    Button,
} from '@mui/material';
import AddCommentIcon from '@mui/icons-material/AddComment';
import { getMyReports } from '@app/model';
import { IUserReport } from '@app/model';
import { ReportSubmitModal } from '../ReportSubmitModal';

// Helper function to render status with appropriate color
const getStatusChip = (status: IUserReport['status']) => {
    switch (status) {
        case 'resolved':
            return <Chip label="已处理" color="success" size="small" />;
        case 'dismissed':
            return <Chip label="已驳回" color="warning" size="small" />;
        case 'pending':
        default:
            return <Chip label="待处理" color="info" size="small" />;
    }
};

export const MyReportsList: React.FC = () => {
    const [reports, setReports] = useState<IUserReport[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const [page, setPage] = useState(1);
    const [totalPages, setTotalPages] = useState(0);
    const [modalOpen, setModalOpen] = useState(false); // State for the modal
    const limit = 10;

    useEffect(() => {
        const fetchReports = async () => {
            setLoading(true);
            setError('');
            try {
                const result = await getMyReports({ page, limit });
                if (result.shellIsOk && result.data) {
                    setReports(result.data?.reports ||[]);
                    setTotalPages(Math.ceil(result.data.total / limit));
                } else {
                    setError(result.err?.message || '您未登录，无法加载举报列表。');
                }
            } catch (e) {
                setError('网络错误，请稍后重试。');
            } finally {
                setLoading(false);
            }
        };

        fetchReports();
    }, [page]);

    const handlePageChange = (event: React.ChangeEvent<unknown>, value: number) => {
        setPage(value);
    };

    if (loading) {
        return (
            <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
                <CircularProgress />
            </Box>
        );
    }

    if (error) {
        return <Alert severity="error">{error}</Alert>;
    }

    return (
        <Box>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h6">
                    我的举报记录
                </Typography>
                <Button
                    variant="contained"
                    startIcon={<AddCommentIcon />}
                    onClick={() => setModalOpen(true)}
                >
                    发起举报
                </Button>
            </Box>

            {reports.length === 0 ? (
                <Typography>您还没有任何举报记录。</Typography>
            ) : (
                <>
                    <List>
                        {reports.map((report, index) => (
                            <React.Fragment key={report.uuid}>
                                <ListItem alignItems="flex-start">
                                    <ListItemText
                                        primary={
                                            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                                <Typography variant="subtitle1">
                                                    举报对象: {report.reported_uuid}
                                                </Typography>
                                                {getStatusChip(report.status)}
                                            </Box>
                                        }
                                        secondary={
                                            <>
                                                <Typography
                                                    sx={{ display: 'block' }}
                                                    component="span"
                                                    variant="body2"
                                                    color="text.primary"
                                                >
                                                    原因: {report.reason}
                                                </Typography>
                                                <Typography variant="caption" color="text.secondary">
                                                    {new Date(report.created_at).toLocaleString()}
                                                </Typography>
                                            </>
                                        }
                                    />
                                </ListItem>
                                {index < reports.length - 1 && <Divider variant="inset" component="li" />}
                            </React.Fragment>
                        ))}
                    </List>
                    {totalPages > 1 && (
                        <Box sx={{ display: 'flex', justifyContent: 'center', mt: 2 }}>
                            <Pagination
                                count={totalPages}
                                page={page}
                                onChange={handlePageChange}
                                color="primary"
                            />
                        </Box>
                    )}
                </>
            )}

            <ReportSubmitModal
                open={modalOpen}
                onClose={() => setModalOpen(false)}
                onSuccess={() => {
                    // Per user instruction, no refresh needed.
                    // A snackbar could be triggered here in a real app.
                    console.log('New report submitted successfully.');
                }}
            />
        </Box>
    );
};
