import { IRequestParams, IResultContent, request } from "@fanfanlo";
import { tms } from "../tms";
import { IUserReport } from "./types"; // 引用通用的类型

// --- 获取我的举报列表 ---

/**
 * @description 获取我的举报列表的请求参数
 */
export interface IGetMyReportsParams {
    page?: number;
    limit?: number;
}

/**
 * @description 获取我的举报列表的响应结果
 */
export interface IGetMyReportsResult {
    is_ok: boolean;
    msg: string;
    reports: IUserReport[];
    total: number;
    page: number;
    limit: number;
}

/**
 * @description 获取当前用户提交的举报列表
 * @param {IGetMyReportsParams} params - 分页参数
 * @returns {Promise<IGetMyReportsResult>}
 */
export async function getMyReports(params: IGetMyReportsParams) {
    const res = await request<IGetMyReportsResult>(
        tms.TReport,
        tms.MReportGetMyList,
        params
    );
    return res
}
